.product{
    display: flex;
    margin: 40px 30px 10px 20px;
}
.product-name{
    margin-left: 30px;
}
.product-name h4{
    color: blue;
}
.product-name button,btn {
    border: none;
    background: orange;
    color: white;
    height: 30px;
    width: 150px;
    border-radius: 5px;
    user-select: none;
}
.product-name button:hover{
    background: rgb(173, 113, 0);
    cursor: pointer;
}
.product-name button:active{
    background: rgb(71, 57, 31);
}
