.header > img{
    height: 80px;
}
.header{
    text-align: center;
}
.header nav{
    background: black;
    line-height: 40px;
    font-size: 20px;
}
nav a{
    text-decoration: none;
    color: white;
    margin-left: 20px;
    padding: 5px;
}
nav a:hover{
    background: orange;
    transition: .2s;
}
#profileimg{
    position: absolute;
    border-radius: 50%;
    width: 35px;
    margin-top: 3px;
    margin-left: 10px;
}
#lgbtn{
    margin-left: 20px;
}